<template>
  <div ref="message" class="message-container">
    <div
      :class="[
        { succes: props.className === 'good' },
        { error: props.className === 'error' },
      ]"
      class="message-title"
    >
      {{ props.title }}
    </div>
    <hr />
    <div v-html="props.note" class="message-note"></div>
    <img
      @click="emits('closeMessage')"
      @mouseover="hover = true"
      @mouseout="hover = false"
      class="cancel-btn"
      :src="
        hover
          ? '../../../resources/img/close-black-purple.png'
          : '../../../resources/img/close-light-purple.png'
      "
      alt=""
    />
  </div>
</template>
<script setup>
import { ref } from "vue";

const emits = defineEmits(["closeMessage"]);
const props = defineProps({
  title: { type: String, required: true },
  note: { type: String, required: true },
  className: { type: String, required: true },
});

const message = ref(null);
const hover = ref(false);
</script>
<style scoped>
.message-container {
  position: fixed;
  bottom: 10px;
  left: 10px;
  width: 300px;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 0 10px 1px rgb(109, 31, 182);
  background: white;
  /* position: absolute; */
  display: flex;
  flex-direction: column;
  gap: 2px;
  z-index: 3;
}
.message-title {
  font-weight: 600;
  font-size: 1.2em;
}
.succes {
  color: rgb(16, 194, 16);
}
.error {
  color: rgb(233, 28, 28);
}
.message-note {
  font-size: 1.1em;
}
.cancel-btn {
  position: absolute;
  background-size: cover;
  width: 14px;
  height: 14px;
  right: 10px;
  top: 12px;
  transition: all 0.3s;
}
.cancel-btn:hover {
  top: 10px;
  cursor: pointer;
}
</style>
