.window-registration[data-v-69fb65b8] {
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.window-registration-title[data-v-69fb65b8] {
  font-size: 2em;
  font-weight: 600;
}
.window-registration-form[data-v-69fb65b8] {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  width: 100%;
}
.form-control[data-v-69fb65b8] {
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 100%;
}
.form-control label[data-v-69fb65b8] {
  font-size: 1.1em;
  text-align: left;
}
.form-control input[data-v-69fb65b8] {
  padding: 6px 12px;
  font-size: 1.2em;
  border-radius: 10px;
  outline: none;
  border: 1px solid;
  background-color: #b9a3d3fa;
  transition: all 0.3s;
}
.form-control input[data-v-69fb65b8]:hover,
.form-control input[data-v-69fb65b8]:focus {
  background-color: #a372dbfa;
}
.form-control-btns[data-v-69fb65b8] {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 10px;
}
.registration-form-btn[data-v-69fb65b8] {
  padding: 8px 14px;
  border-radius: 10px;
  border: none;
  background-color: #b9a3d3fa;
  font-size: 1em;
  transition: all 0.3s;
}
.registration-form-btn[data-v-69fb65b8]:hover {
  background-color: #a372dbfa;
  box-shadow: 0 0 3px 1px #2d015fe7;
}
.registration-form-to-login[data-v-69fb65b8] {
  padding: 8px 14px;
  text-align: center;
  border-radius: 10px;
  font-size: 1em;
  /* margin-top: 10px; */
  transition: 0.3s;
  cursor: pointer;
  background-color: #cbcacce7;
  border: none;
}
.registration-form-to-login[data-v-69fb65b8]:hover {
  color: #2d015fe7;
  text-decoration: dashed;
  box-shadow: 0 0 3px 1px #2d015fe7;
}
