abel for=\"passReg\">Пароль</label>\r\n        <input\r\n          v-model=\"pass\"\r\n          name=\"passReg\"\r\n          class=\"inp-pass-reg\"\r\n          type=\"password\"\r\n          placeholder=\"Пароль\"\r\n        />\r\n      </div>\r\n      <div class=\"form-control\">\r\n        <label for=\"passReg2\">Повторите пароль</label>\r\n        <input\r\n          v-model=\"repPass\"\r\n          name=\"passReg2\"\r\n          class=\"inp-pass2-reg\"\r\n          type=\"password\"\r\n          placeholder=\"Повторите пароль\"\r\n        />\r\n      </div>\r\n      <div class=\"form-control-btns\">\r\n        <button\r\n          @click=\"\r\n            () => {\r\n              registration();\r\n            }\r\n          \"\r\n          class=\"registration-form-btn\"\r\n        >\r\n          Зарегистрироваться\r\n        </button>\r\n        <button\r\n          class=\"registration-form-to-login\"\r\n          @click=\"\r\n            () => {\r\n              emits('backToLoginForm');\r\n            }\r\n          \"\r\n        >\r\n          Назад\r\n        </button>\r\n      </div>\r\n    </div>\r\n  </div>\r\n  <Teleport to=\"body\">\r\n    <Message\r\n      v-if=\"openMessage === true\"\r\n      :title=\"titleForMsg\"\r\n      :note=\"noteForMsg\"\r\n      :className=\"classNameForMsg\"\r\n      @close-message=\"openMessage = false\"\r\n    ></Message>\r\n  </Teleport>\r\n</template>\r\n<script setup>\r\nimport { ref, inject } from \"vue\";\r\nimport Message from \"../../message/message.vue\";\r\n\r\nconst emits = defineEmits([\"backToLoginForm\", \"unmountRegForm\"]);\r\n\r\nconst titleForMsg = ref(\"\");\r\nconst noteForMsg = ref(\"\");\r\nconst classNameForMsg = ref(\"\");\r\nconst openMessage = ref(false);\r\n\r\nconst name = ref(\"\");\r\nconst phone = ref(\"\");\r\nconst email = ref(\"\");\r\nconst pass = ref(\"\");\r\nconst repPass = ref(\"\");\r\nconst checkUser = inject(\"checkUser\");\r\n\r\nfunction isValidNumber(phone) {\r\n  const pattern = /7\\d{10}/g;\r\n  return pattern.test(phone);\r\n}\r\n\r\nfunction isValidEmail(email) {\r\n  const pattern =\r\n    /^(([^<>()\\[\\]\\\\.,;:\\s@\"]+(\\.[^<>()\\[\\]\\\\.,;:\\s@\"]+)*)|(\".+\"))@((\\[[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}])|(([a-zA-Z\\-0-9]+\\.)+[a-zA-Z]{2,}))$/;\r\n  return pattern.test(email);\r\n}\r\n\r\nfunction isValidPass(pass) {\r\n  const pattern = /^(?=.*\\d)(?=.*[a-z])(?=.*[A-Z])[a-zA-Z0-9]{8,20}$/;\r\n  return pattern.test(pass);\r\n}\r\n\r\nasync function registration() {\r\n  try {\r\n    let noteErr = \"\";\r\n    const users = await fetch(\"/api/user/access\");\r\n    const arrayUser = await users.json();\r\n    for (let i = 0; i < arrayUser.length; i++) {\r\n      if (arrayUser[i].phone === phone.value) {\r\n        titleForMsg.value = \"Ошибка заполнения!\";\r\n        noteErr += \"Пользователь с таким номер уже существует\";\r\n        classNameForMsg.value = \"error\";\r\n        openMessage.value = true;\r\n        noteForMsg.value = noteErr;\r\n        return;\r\n      }\r\n      if (arrayUser[i].email === email.value) {\r\n        titleForMsg.value = \"Ошибка заполнения!\";\r\n        noteErr += \"Пользователь с такой почтой уже существует\";\r\n        classNameForMsg.value = \"error\";\r\n        openMessage.value = true;\r\n        noteForMsg.value = noteErr;\r\n        return;\r\n      }\r\n    }\r\n    // if (\r\n    // name.value.length === 0 ||\r\n    // phone.value.length === 0 ||\r\n    // email.phone.length === 0 ||\r\n    // pass.value.length === 0\r\n    // ) {\r\n    // titleForMsg.value = \"Ошибка заполнения!\";\r\n    // noteErr += \"Заполните поля!\";\r\n    // classNameForMsg.value = \"error\";\r\n    // openMessage.value = true;\r\n    // }\r\n    if (!isValidNumber(phone.value)) {\r\n      titleForMsg.value = \"Ошибка заполнения!\";\r\n      noteErr += \"<br>Неверный номер телефона!\";\r\n      classNameForMsg.value = \"error\";\r\n      openMessage.value = true;\r\n    }\r\n    if (!isValidPass(pass.value)) {\r\n      titleForMsg.value = \"Ошибка заполнения!\";\r\n      noteErr +=\r\n        \"<br>Неверный пароль: <br>Пароль должен состоять из: <br>Минимум одной заглавной буквы <br> Минимум одной строчной буквы <br>Минимум из одной цифры\";\r\n      classNameForMsg.value = \"error\";\r\n      openMessage.value = true;\r\n    }\r\n    if (!isValidEmail(email.value)) {\r\n      titleForMsg.value = \"Ошибка заполнения!\";\r\n      noteErr += \"<br>Неверная почта\";\r\n      classNameForMsg.value = \"error\";\r\n      openMessage.value = true;\r\n    }\r\n    if (pass.value != repPass.value) {\r\n      titleForMsg.value = \"Ошибка заполнения!\";\r\n      noteErr += \"<br>Пароли не совпадает\";\r\n      classNameForMsg.value = \"error\";\r\n      openMessage.value = true;\r\n    }\r\n    noteForMsg.value = noteErr;\r\n\r\n    if (noteErr.length === 0) {\r\n      const data = {\r\n        nameUs: name.value,\r\n        phone: phone.value,\r\n        email: email.value,\r\n        pass: pass.value,\r\n      };\r\n      console.log(data);\r\n      openMessage.value = false;\r\n      const response = await fetch(\"/api/conf/data/registration\", {\r\n        method: \"POST\",\r\n        headers: {\r\n          \"Content-Type\": \"application/json\",\r\n        },\r\n        body: JSON.stringify(data),\r\n      });\r\n      if (!response.ok) {\r\n        throw new Error(`Ошибка ${response.status}: ${response.statusText}`);\r\n      }\r\n      const res = await fetch(`/api/login`, {\r\n        headers: {\r\n          Authorization: `Basic ${btoa(`${phone.value}:${pass.value}`)}`,\r\n        },\r\n      });\r\n      checkUser();\r\n      emits(\"unmountRegForm\");\r\n    }\r\n  } catch (error) {\r\n    console.log(error);\r\n  }\r\n}\r\n</script>\r\n<style scoped>\r\n.window-registration {\r\n  padding: 30px;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  gap: 10px;\r\n}\r\n\r\n.window-registration-title {\r\n  font-size: 2em;\r\n  font-weight: 600;\r\n}\r\n\r\n.window-registration-form {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  gap: 10px;\r\n  width: 100%;\r\n}\r\n.form-control {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 5px;\r\n  width: 100%;\r\n}\r\n.form-control label {\r\n  font-size: 1.1em;\r\n  text-align: left;\r\n}\r\n.form-control input {\r\n  padding: 6px 12px;\r\n  font-size: 1.2em;\r\n  border-radius: 10px;\r\n  outline: none;\r\n  border: 1px solid;\r\n  background-color: #b9a3d3fa;\r\n  transition: all 0.3s;\r\n}\r\n.form-control input:hover,\r\n.form-control input:focus {\r\n  background-color: #a372dbfa;\r\n}\r\n.form-control-btns {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  width: 100%;\r\n  margin-top: 10px;\r\n}\r\n.registration-form-btn {\r\n  padding: 8px 14px;\r\n  border-radius: 10px;\r\n  border: none;\r\n  background-color: #b9a3d3fa;\r\n  font-size: 1em;\r\n  transition: all 0.3s;\r\n}\r\n.registration-form-btn:hover {\r\n  background-color: #a372dbfa;\r\n  box-shadow: 0 0 3px 1px #2d015fe7;\r\n}\r\n.registration-form-to-login {\r\n  padding: 8px 14px;\r\n  text-align: center;\r\n  border-radius: 10px;\r\n  font-size: 1em;\r\n  /* margin-top: 10px; */\r\n  transition: 0.3s;\r\n  cursor: pointer;\r\n  background-color: #cbcacce7;\r\n  border: none;\r\n}\r\n\r\n.registration-form-to-login:hover {\r\n  color: #2d015fe7;\r\n  text-decoration: dashed;\r\n  box-shadow: 0 0 3px 1px #2d015fe7;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
