<template>
  <div class="datetime">
    <div class="datetime-block">
      <div class="datetime-title">Расписание тренировок</div>
      <div class="datetime-content">
        <div
          v-for="day in week"
          :key="day"
          @click="
            () => {
              refactorStringDay(day);
            }
          "
          class="datetime-content-block"
          :class="[
            {
              'datetime-content-block-active':
                activeDay.slice(8, 10) === day.slice(-13, -11),
            },
          ]"
        >
          <div class="datetime-day">
            {{ day.split(", ")[0] }}
          </div>
          <div @click="" class="datetime-day">
            {{ day.split(", ")[1].split(".")[0] }}
          </div>
        </div>
      </div>
      <div class="datetime-programs-content">
        <CardRecord
          :record-data="recordData"
          :active-day="activeDay"
        ></CardRecord>
      </div>
    </div>
  </div>
</template>
<script setup>
import { onMounted, ref, watch } from "vue";
import CardRecord from "./components/card-record.vue";

const week = ref([]);
const recordData = ref([]);
const winWidth = ref(0);
const activeDay = ref("");
const refactorDay = ref("");

const date = new Date();
const long = {
  weekday: "long",
  day: "numeric",
  month: "numeric",
  year: "numeric",
};
const short = {
  weekday: "short",
  day: "numeric",
  month: "numeric",
  year: "numeric",
};
watch(winWidth, () => {
  winWidth.value = window.innerWidth;
  if (winWidth.value > 900) {
    createWeekCalendar(date, long);
  } else {
    createWeekCalendar(date, short);
  }
});

const onResize = () => (winWidth.value = window.innerWidth);

function refactorStringDay(day) {
  const usuallyDay = day.split(" ")[1];
  activeDay.value = usuallyDay.split(".").reverse().join("-");
  refactorDay.value = activeDay.value;
}

onMounted(async () => {
  createWeekCalendar(date, long);
  window.addEventListener("resize", onResize);
  const res = await fetch("/api/programs/getschedule");
  recordData.value = await res.json();
  refactorStringDay(week.value[0]);
});

function createWeekCalendar(startDate, format) {
  const start = new Date(startDate);
  const weekDays = [];
  // Заполняем массив днями недели
  for (let i = 0; i < 7; i++) {
    const day = new Date(start);
    day.setDate(start.getDate() + i);
    weekDays.push(day.toLocaleDateString("ru-RU", format));
  }
  week.value = weekDays;
}
</script>
<style scoped>
.datetime-programs-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  width: 100%;
}

.datetime {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  width: 100%;
}
.datetime-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1400px;
}
.datetime-title {
  font-size: 2em;
  font-weight: 600;
  padding: 12px;
}
.datetime-content {
  width: 1400px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.datetime-day,
.datetime-number {
  font-size: 1.5em;
  text-transform: capitalize;
}
.datetime-content-block {
  display: flex;
  flex-direction: column;
  gap: 5px;
  align-items: center;
  border-radius: 20px;
  padding: 10px 20px;
  box-shadow: 0px 0px 20px #b9a3d3fa;
  transition: 0.3s;
}
.datetime-content-block-active {
  background-color: #a372dbfa;
}
.datetime-content-block:hover {
  background-color: #a372dbfa;
}
</style>
