.window-login[data-v-d4dae83c] {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.window-login-title[data-v-d4dae83c] {
  font-size: 2em;
  font-weight: 600;
}
.window-login-form[data-v-d4dae83c] {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
}
.form-control[data-v-d4dae83c] {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 100%;
}
.form-control label[data-v-d4dae83c] {
  font-size: 1.1em;
  text-align: left;
}
.form-control input[data-v-d4dae83c] {
  padding: 6px 12px;
  font-size: 1.2em;
  border-radius: 10px;
  outline: none;
  border: 1px solid;
  background-color: #b9a3d3fa;
  transition: all 0.3s;
}
.form-control input[data-v-d4dae83c]:hover,
.form-control input[data-v-d4dae83c]:focus {
  background-color: #a372dbfa;
}
.login-form-btn[data-v-d4dae83c] {
  padding: 8px 14px;
  border-radius: 10px;
  border: none;
  background-color: #b9a3d3fa;
  font-size: 1em;
  transition: all 0.3s;
}
.login-form-btn[data-v-d4dae83c]:hover {
  background-color: #a372dbfa;
  box-shadow: 0 0 3px 1px #2d015fe7;
}
.form-control-btn[data-v-d4dae83c] {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.cancel-btn[data-v-d4dae83c] {
  padding: 8px 14px;
  text-align: center;
  border-radius: 10px;
  font-size: 1em;
  /* margin-top: 10px; */
  transition: 0.3s;
  cursor: pointer;
  background-color: #cbcacce7;
  border: none;
}
.cancel-btn[data-v-d4dae83c]:hover {
  color: #2d015fe7;
  text-decoration: dashed;
  box-shadow: 0 0 3px 1px #2d015fe7;
}
