\n    ></Message>\r\n  </Teleport>\r\n</template>\r\n<script setup>\r\nimport { inject, ref } from \"vue\";\r\nimport Message from \"../../message/message.vue\";\r\n\r\nconst emits = defineEmits([\"unmountLoginForm\"]);\r\n\r\nconst checkUser = inject(\"checkUser\");\r\n\r\nconst phone = ref(\"\");\r\nconst pass = ref(\"\");\r\nconst titleForMsg = ref(\"\");\r\nconst noteForMsg = ref(\"\");\r\nconst classNameForMsg = ref(\"\");\r\nconst openMessage = ref(false);\r\n\r\nfunction isValidNumber(phone) {\r\n  const pattern = /7\\d{10}/g;\r\n  return pattern.test(phone);\r\n}\r\n\r\nfunction isValidPass(pass) {\r\n  const pattern = /^(?=.*\\d)(?=.*[a-z])(?=.*[A-Z])[a-zA-Z0-9]{8,20}$/;\r\n  return pattern.test(pass);\r\n}\r\n\r\nasync function logining() {\r\n  let noteErr = \"\";\r\n  if (!isValidNumber(phone.value)) {\r\n    titleForMsg.value = \"Ошибка заполнения!\";\r\n    noteErr += \"Неверный номер телефона!\";\r\n    classNameForMsg.value = \"error\";\r\n    openMessage.value = true;\r\n  }\r\n  if (!isValidPass(pass.value)) {\r\n    titleForMsg.value = \"Ошибка заполнения!\";\r\n    noteErr +=\r\n      'Неверный пароль: <br>Пароль должен состоять из: <br>Минимум одной заглавной буквы <br> Минимум одной строчной буквы <br>Минимум из одной цифры\"';\r\n    classNameForMsg.value = \"error\";\r\n    openMessage.value = true;\r\n  }\r\n\r\n  noteForMsg.value = noteErr;\r\n  if (noteErr.length === 0) {\r\n    openMessage.value = false;\r\n    const res = await fetch(`/api/login`, {\r\n      headers: {\r\n        Authorization: `Basic ${btoa(`${phone.value}:${pass.value}`)}`,\r\n      },\r\n    });\r\n    // const loginEvent = new CustomEvent(\"login\", {\r\n    // detail: phone.value,\r\n    // });\r\n    // document.dispatchEvent(loginEvent);\r\n    checkUser();\r\n    emits(\"unmountLoginForm\");\r\n  }\r\n}\r\n</script>\r\n<style scoped>\r\n.window-login {\r\n  padding: 20px;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  gap: 10px;\r\n}\r\n.window-login-title {\r\n  font-size: 2em;\r\n  font-weight: 600;\r\n}\r\n.window-login-form {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  gap: 20px;\r\n  width: 100%;\r\n}\r\n.form-control {\r\n  position: relative;\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 5px;\r\n  width: 100%;\r\n}\r\n.form-control label {\r\n  font-size: 1.1em;\r\n  text-align: left;\r\n}\r\n.form-control input {\r\n  padding: 6px 12px;\r\n  font-size: 1.2em;\r\n  border-radius: 10px;\r\n  outline: none;\r\n  border: 1px solid;\r\n  background-color: #b9a3d3fa;\r\n  transition: all 0.3s;\r\n}\r\n.form-control input:hover,\r\n.form-control input:focus {\r\n  background-color: #a372dbfa;\r\n}\r\n.login-form-btn {\r\n  padding: 8px 14px;\r\n  border-radius: 10px;\r\n  border: none;\r\n  background-color: #b9a3d3fa;\r\n  font-size: 1em;\r\n  transition: all 0.3s;\r\n}\r\n.login-form-btn:hover {\r\n  background-color: #a372dbfa;\r\n  box-shadow: 0 0 3px 1px #2d015fe7;\r\n}\r\n.form-control-btn {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  width: 100%;\r\n}\r\n.cancel-btn {\r\n  padding: 8px 14px;\r\n  text-align: center;\r\n  border-radius: 10px;\r\n  font-size: 1em;\r\n  /* margin-top: 10px; */\r\n  transition: 0.3s;\r\n  cursor: pointer;\r\n  background-color: #cbcacce7;\r\n  border: none;\r\n}\r\n.cancel-btn:hover {\r\n  color: #2d015fe7;\r\n  text-decoration: dashed;\r\n  box-shadow: 0 0 3px 1px #2d015fe7;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
