<template>
  <div
    ref="window"
    class="back-div"
    @click.self="
      () => {
        $emit('unmountMainForm');
      }
    "
  >
    <div ref="mainContainer" class="main-container">
      <LoginForm
        @unmount-login-form="
          () => {
            $emit('unmountMainForm');
          }
        "
        v-if="enterForm"
      ></LoginForm>
      <RegistrationForm
        @back-to-login-form="
          () => {
            registerForm = false;
            enterForm = true;
            mainContainer.style.top = '25%';
          }
        "
        @unmount-reg-form="
          () => {
            $emit('unmountMainForm');
          }
        "
        v-if="registerForm"
      ></RegistrationForm>
      <div v-if="enterForm" @click="registration" class="login-form-to-reg">
        Регистрация
      </div>
      <img
        @click="$emit('unmountMainForm')"
        @mouseover="hover = true"
        @mouseout="hover = false"
        class="window-img"
        :src="
          hover
            ? '../../../resources/img/close-black-purple.png'
            : '../../../resources/img/close-light-purple.png'
        "
        alt=""
      />
    </div>
  </div>
</template>
<script setup>
import { ref } from "vue";
import LoginForm from "./components/login-form.vue";
import RegistrationForm from "./components/registration-form.vue";

const emits = defineEmits(["unmountMainForm"]);

const registerForm = ref(false);
const enterForm = ref(true);
const mainContainer = ref(null);
const hover = ref(false);

function registration() {
  enterForm.value = false;
  registerForm.value = true;
  mainContainer.value.style.top = "20%";
}
</script>
<style scoped>
.back-div {
  width: 100%;
  height: 100%;
  background: #000000b0;
  position: fixed;
  z-index: 2;
  top: 0;
  right: 0;
}
.main-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 30%;
  height: auto;
  max-height: 60%;
  text-align: center;
  font-size: 1em;
  color: #444;
  background-color: white;
  opacity: 1;
  border-radius: 10px;
  box-shadow: 0 0 13px 4px #0000004d;
  transition: 0.2s ease-out;
  top: 25%;
  right: 35%;
  position: absolute;
  z-index: 3;
  gap: 5%;
  padding: 15px 0;
}
.window-img {
  position: absolute;
  background-size: cover;
  z-index: 4;
  width: 14px;
  height: 14px;
  right: 10px;
  top: 10px;
  transition: all 0.3s;
  cursor: pointer;
}
.window-img:hover {
  top: 8px;
}
</style>
