<template>
  <UserProfile v-if="username" :active-page="activePage"></UserProfile>
  <div class="header">
    <div class="header-sidenav">
      <router-link @click="activePage = ''" to="/" class="header-sidenav-item"
        >Главная</router-link
      >
      <router-link
        @click="activePage = 'schedule'"
        to="/schedule"
        class="header-sidenav-item"
        >Расписание</router-link
      >
      <router-link
        @click="activePage = 'treners'"
        to="/treners"
        class="header-sidenav-item"
        >Тренеры</router-link
      >
      <router-link
        @click="activePage = 'profile'"
        to="/profile"
        class="header-sidenav-item"
        >Профиль</router-link
      >
      <router-link
        v-if="userData.role === 'admin'"
        @click="activePage = 'admin'"
        to="/admin"
        class="header-sidenav-item"
        >Админ</router-link
      >
      <div
        v-if="userData.logged_in == false"
        class="header-sidenav-item btn-login"
        @click="mainForm = true"
      >
        <img
          class="btn-login-img"
          src="../resources/img/enter.png"
          alt="enter"
        />
      </div>
    </div>
  </div>
  <!-- <Header></Header> -->
  <router-view v-slot="{ Component }">
    <keep-alive>
      <component :is="Component"></component>
    </keep-alive>
  </router-view>
  <Teleport to="body">
    <MainRegLoginForm
      v-if="mainForm"
      @unmount-main-form="mainForm = false"
    ></MainRegLoginForm>
  </Teleport>
  <!-- <Landing></Landing> -->
  <!-- <Slider></Slider> -->
  <!-- <ClubPass></ClubPass> -->
  <!-- <Tariff></Tariff> -->
  <!-- <Support></Support> -->
  <!-- <PersTreners></PersTreners> -->
  <!-- <Programs></Programs> -->
  <!-- <Schedule></Schedule> -->
  <!-- <TrenersHeader></TrenersHeader> -->
  <!-- <PersTreners></PersTreners> -->
  <!-- <GroupsTreners></GroupsTreners> -->
  <!-- <ModelFilter></ModelFilter> -->
</template>
<script setup>
import { onMounted, provide, ref, watch } from "vue";
import MainRegLoginForm from "./vue-modules/login-feature/mainRegLoginForm.vue";
import Message from "./vue-modules/message/message.vue";
import UserProfile from "./vue-modules/user-profile/user-profile.vue";

const mainForm = ref(false);
const userData = ref([]);
const username = ref(undefined);
const activePage = ref("");
const enterTimeout = ref(undefined);

provide("userName", username);
provide("checkUser", checkUser);

watch(userData, () => {
  console.log(userData.value);
});

onMounted(() => {
  checkUser();
  document.addEventListener("login", checkUser);
  const page = window.location.href.split("/")[3];
  activePage.value = page;
});

// function shiftLoginForm(){
// clearTimeout(enterTimeout.value);
// enterTimeout.value = setTimeout(()=>{
// if(username.value)
// })
// }

async function checkUser() {
  const res = await fetch("/api/user");
  userData.value = await res.json();
  console.log(userData.value);
  username.value = userData.value.name;
  console.log(username.value);
}
</script>
<style>
.header {
  width: 100%;
  display: flex;
  margin: 10px 0;
  border-bottom: 1px solid #2d015fe7;
  border-top: 1px solid #2d015fe7;
}
.header-sidenav {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 50px;
  padding: 10px;
  margin: 0 auto;
}
.header-sidenav-item {
  font-size: 2em;
  font-weight: 600;
  padding: 10px;
  transition: all 0.3s;
  border-radius: 20px;
  text-decoration: none;
  color: black;
}
.sidenav-item-active {
  color: #2d015fe7;
  background-color: #2d015f67;
}
.header-sidenav-item:hover {
  color: #2d015fe7;
  background-color: #2d015f67;
}

.btn-login {
  width: 56px;
  display: flex;
  position: absolute;
  right: 10%;
}
.btn-login img {
  height: 30px;
}
</style>
