.slider[data-v-39d0a44c] {
  margin: 10px auto;
  position: relative;
  width: 1400px; /* Ширина слайдера */
  height: 400px; /* Высота слайдера */
  overflow: hidden;
  border-radius: 10px; /* Скругление углов */
}
.slides[data-v-39d0a44c] {
  display: flex;
  transition: transform 0.5s ease-in-out; /* Плавный переход */
  animation: slide 8s infinite;
}
.slide[data-v-39d0a44c] {
  min-width: 100%; /* Каждая картинка занимает всю ширину слайдера */
}
.slide-content[data-v-39d0a44c] {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: 380px;
  margin: 10px;
  border-radius: 10px;
}
.color[data-v-39d0a44c] {
  background: rgb(4, 224, 18);
  background: linear-gradient(
    125deg,
    rgba(4, 224, 18, 1) 28%,
    rgba(85, 251, 199, 1) 58%
  );
}
.color2[data-v-39d0a44c] {
  background: rgb(4, 224, 199);
  background: linear-gradient(
    125deg,
    rgba(4, 224, 199, 1) 28%,
    rgba(155, 251, 85, 1) 58%
  );
}

/* @keyframes slide { */
/* 0% { transform: translateX(0); } */
/* 25% { transform: translateX(0); } */
/* 30% { transform: translateX(-100%); } */
/* 55% { transform: translateX(-100%); } */
/* 60% { transform: translateX(0); } */
/* 100% { transform: translateX(0); } */
/* } */
.slide-content-block[data-v-39d0a44c] {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  padding: 70px 0;
  height: 100%;
}
.slide-content-title[data-v-39d0a44c] {
  font-size: 3em;
  font-weight: 600;
}
.slide-content-note[data-v-39d0a44c] {
  font-size: 2em;
}
.slide-content-btn[data-v-39d0a44c] {
  font-size: 1.6em;
  font-weight: 600;
  border-radius: 50px;
  padding: 10px 16px;
}
.btn-color[data-v-39d0a44c] {
  background-color: rgba(85, 251, 199, 1);
}
.btn-color2[data-v-39d0a44c] {
  background-color: rgba(155, 251, 85, 1);
}
