<template>
  <div class="filter">
    <div class="filter-block">
      <div class="filter-block-title">Фильтр</div>
      <div class="filter-category-block">
        <div class="filter-category-title">Профиль</div>
        <div class="filter-category-row filter-category">
          <input type="checkbox" v-model="persTrener" />
          <label
            :class="[{ 'category-item-active': persTrener === true }]"
            class="filter-category-item"
            @click="persTrener = !persTrener"
            >Персональный тренер</label
          >
          <input type="checkbox" v-model="groupTrener" />
          <label
            class="filter-category-item"
            :class="[{ 'category-item-active': groupTrener === true }]"
            @click="groupTrener = !groupTrener"
            >Тренер групповых программ</label
          >
        </div>
      </div>
      <div class="filter-category-block">
        <div class="filter-category-title">Пол тренера</div>
        <div class="filter-category-row filter-gender">
          <input type="checkbox" v-model="noMatter" />
          <label
            class="filter-gender-item"
            :class="[{ 'gender-item-active': noMatter === true }]"
            @click="
              () => {
                noMatter = !noMatter;
                filterMan = false;
                filterWomen = false;
              }
            "
            >Неважно</label
          >
          <input type="checkbox" v-model="filterMan" />
          <label
            for=""
            :class="[{ 'gender-item-active': filterMan === true }]"
            @click="
              () => {
                filterMan = !filterMan;
                noMatter = false;
              }
            "
            class="filter-gender-item"
            >Мужской</label
          >
          <input type="checkbox" v-model="filterWomen" />
          <label
            class="filter-gender-item"
            :class="[{ 'gender-item-active': filterWomen === true }]"
            @click="
              () => {
                filterWomen = !filterWomen;
                noMatter = false;
              }
            "
            >Женский</label
          >
        </div>
      </div>
      <div class="filter-category-block">
        <div class="filter-category-title">Стаж</div>
        <div class="filter-category-row input-range-row">
          <span class="value" id="minValue">{{ minRange }}</span>
          <div class="rangeslider">
            <input
              type="range"
              v-model="minRange"
              class="min input-ranges"
              id="rangeMin"
              min="0"
              max="37"
            />
            <input
              v-model="maxRange"
              type="range"
              class="max input-ranges"
              id="rangeMax"
              min="0"
              max="37"
            />
            <div class="slider" ref="slider" id="slider"></div>
          </div>
          <span class="value" id="maxValue">{{ maxRange }}</span>
        </div>
      </div>
      <div
        @click="
          filterOpen = true;
          emits('openFilter', filterOpen);
        "
        class="filter-block-btn"
      >
        Показать <span class="amount-treners"></span> тренеров
      </div>
    </div>
  </div>
</template>
<script setup>
import { onMounted, ref, watch, defineEmits } from "vue";

const emits = defineEmits(["openFilter"]);

const minRange = ref(10);
const maxRange = ref(30);
const filterOpen = ref(false);
const slider = ref(null);
const filterMan = ref(false);
const filterWomen = ref(false);
const noMatter = ref(false);
const persTrener = ref(false);
const groupTrener = ref(false);

onMounted(() => {
  updateSlider();
});

watch([minRange, maxRange], () => {
  updateSlider();
});

function updateSlider() {
  const min = parseInt(rangeMin.value);
  const max = parseInt(rangeMax.value);

  // Обеспечиваем, чтобы минимальное значение не превышало максимальное
  if (min > max) {
    rangeMin.value = max;
  }

  // Обновляем положение ползунка
  const minPercent = (rangeMin.value / rangeMin.max) * 100;
  const maxPercent = (rangeMax.value / rangeMax.max) * 100;

  slider.value.style.left = `${minPercent}%`;
  slider.value.style.width = `${maxPercent - minPercent}%`;

  // Обновляем отображаемые значения
  minValue.textContent = rangeMin.value;
  maxValue.textContent = rangeMax.value;
}
</script>
<style scoped>
/* Фильтр */
.filter {
  width: 100%;
  /* display: none; */
  transition: all 3s;
  margin: 30px 0;
  /* opacity: 0; */
}
.filter-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* width: 1400px; */
  gap: 30px;
  margin: 0 auto;
  border-radius: 20px;
  background-color: #cccccc62;
  padding: 10px;
}
.filter-block-title {
  font-size: 2em;
  font-weight: 600;
}
.filter-category-title {
  font-size: 1.6em;
  font-weight: 600;
}
.filter-category-row {
  display: flex;
  flex-direction: row;
  gap: 20px;
}
.filter-category-block {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
}
.filter-category-item,
.filter-gender-item {
  font-size: 1.2em;
  padding: 5px 10px;
  border-radius: 20px;
  border: 1px solid #2d015fe7;
  transition: all 0.3s;
}
.filter-category-item:hover,
.filter-gender-item:hover {
  background-color: #b9a3d3fa;
}
.gender-item-active,
.category-item-active {
  background-color: #a372dbfa;
  color: white;
}

/* Ползунок */
.input-range-row {
  padding: 10px;
}
.value {
  font-size: 1.5em;
  width: 30px;
}
/* Range Slider */
.input-ranges[type="range"] {
  width: 260px;
  height: 30px;
  overflow: hidden;
  outline: none;
}

.input-ranges[type="range"],
.input-ranges[type="range"]::-webkit-slider-runnable-track,
.input-ranges[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  background: none;
}

input[type="checkbox"] {
  display: none;
}
.input-ranges[type="range"]::-webkit-slider-runnable-track {
  width: 200px;
  height: 2px;
  border-radius: 20px;
  background: var(--color-headline);
}

.input-ranges[type="range"]:nth-child(2)::-webkit-slider-runnable-track {
  background: #ccccccfa;
  height: 2px;
  border-radius: 20px;
}

.input-ranges[type="range"]::-webkit-slider-thumb {
  position: relative;
  height: 15px;
  width: 15px;
  margin-top: -7px;
  background: #a372dbfa;
  border: 1px solid var(--color-headline);
  border-radius: 25px;
  cursor: pointer;
  z-index: 1;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}
/* Кнопки при наведении */
.input-ranges[type="range"]::-webkit-slider-thumb:hover {
  background: #2d015fe7;
  border: 1px solid var(--color-primary);
  outline: 0.5px solid var(--color-primary);
}

.input-ranges[type="range"]::-webkit-slider-thumb:active {
  cursor: grabbing;
}

.input-ranges[type="range"]:nth-child(1)::-webkit-slider-thumb {
  z-index: 2;
}

.rangeslider {
  font-family: sans-serif;
  font-size: 14px;
  position: relative;
  height: 20px;
  width: 260px;
  display: inline-block;
  margin-top: -5px;
}

.rangeslider input {
  position: absolute;
}
/* Стили для "ползунка" */
.slider {
  position: absolute;
  top: 0;
  height: 10px;
  background: #b9a3d3fa; /* Цвет ползунка */
  margin-top: 10px;
  border-radius: 5px;
  pointer-events: none; /* Запрет на взаимодействие с этим элементом */
}
/*  */
.filter-block-btn {
  padding: 10px 14px;
  font-weight: 600;
  font-size: 1.5em;
  background-color: #a372dbfa;
  border-radius: 20px;
  color: black;
  transition: all 0.3s;
}
.filter-block-btn:hover {
  color: white;
  background-color: #2d015fe7;
}
</style>
