.header {
  width: 100%;
  display: flex;
  margin: 10px 0;
  border-bottom: 1px solid #2d015fe7;
  border-top: 1px solid #2d015fe7;
}
.header-sidenav {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 50px;
  padding: 10px;
  margin: 0 auto;
}
.header-sidenav-item {
  font-size: 2em;
  font-weight: 600;
  padding: 10px;
  transition: all 0.3s;
  border-radius: 20px;
  text-decoration: none;
  color: black;
}
.sidenav-item-active {
  color: #2d015fe7;
  background-color: #2d015f67;
}
.header-sidenav-item:hover {
  color: #2d015fe7;
  background-color: #2d015f67;
}
.btn-login {
  width: 56px;
  display: flex;
  position: absolute;
  right: 10%;
}
.btn-login img {
  height: 30px;
}
