t :is=\"Component\"></component>\r\n    </keep-alive>\r\n  </router-view>\r\n  <Teleport to=\"body\">\r\n    <MainRegLoginForm\r\n      v-if=\"mainForm\"\r\n      @unmount-main-form=\"mainForm = false\"\r\n    ></MainRegLoginForm>\r\n  </Teleport>\r\n  <!-- <Landing></Landing> -->\r\n  <!-- <Slider></Slider> -->\r\n  <!-- <ClubPass></ClubPass> -->\r\n  <!-- <Tariff></Tariff> -->\r\n  <!-- <Support></Support> -->\r\n  <!-- <PersTreners></PersTreners> -->\r\n  <!-- <Programs></Programs> -->\r\n  <!-- <Schedule></Schedule> -->\r\n  <!-- <TrenersHeader></TrenersHeader> -->\r\n  <!-- <PersTreners></PersTreners> -->\r\n  <!-- <GroupsTreners></GroupsTreners> -->\r\n  <!-- <ModelFilter></ModelFilter> -->\r\n</template>\r\n<script setup>\r\nimport { onMounted, provide, ref, watch } from \"vue\";\r\nimport MainRegLoginForm from \"./vue-modules/login-feature/mainRegLoginForm.vue\";\r\nimport Message from \"./vue-modules/message/message.vue\";\r\nimport UserProfile from \"./vue-modules/user-profile/user-profile.vue\";\r\n\r\nconst mainForm = ref(false);\r\nconst userData = ref([]);\r\nconst username = ref(undefined);\r\nconst activePage = ref(\"\");\r\nconst enterTimeout = ref(undefined);\r\n\r\nprovide(\"userName\", username);\r\nprovide(\"checkUser\", checkUser);\r\n\r\nwatch(userData, () => {\r\n  console.log(userData.value);\r\n});\r\n\r\nonMounted(() => {\r\n  checkUser();\r\n  document.addEventListener(\"login\", checkUser);\r\n  const page = window.location.href.split(\"/\")[3];\r\n  activePage.value = page;\r\n});\r\n\r\n// function shiftLoginForm(){\r\n// clearTimeout(enterTimeout.value);\r\n// enterTimeout.value = setTimeout(()=>{\r\n// if(username.value)\r\n// })\r\n// }\r\n\r\nasync function checkUser() {\r\n  const res = await fetch(\"/api/user\");\r\n  userData.value = await res.json();\r\n  console.log(userData.value);\r\n  username.value = userData.value.name;\r\n  console.log(username.value);\r\n}\r\n</script>\r\n<style>\r\n.header {\r\n  width: 100%;\r\n  display: flex;\r\n  margin: 10px 0;\r\n  border-bottom: 1px solid #2d015fe7;\r\n  border-top: 1px solid #2d015fe7;\r\n}\r\n.header-sidenav {\r\n  display: flex;\r\n  flex-direction: row;\r\n  align-items: center;\r\n  gap: 50px;\r\n  padding: 10px;\r\n  margin: 0 auto;\r\n}\r\n.header-sidenav-item {\r\n  font-size: 2em;\r\n  font-weight: 600;\r\n  padding: 10px;\r\n  transition: all 0.3s;\r\n  border-radius: 20px;\r\n  text-decoration: none;\r\n  color: black;\r\n}\r\n.sidenav-item-active {\r\n  color: #2d015fe7;\r\n  background-color: #2d015f67;\r\n}\r\n.header-sidenav-item:hover {\r\n  color: #2d015fe7;\r\n  background-color: #2d015f67;\r\n}\r\n\r\n.btn-login {\r\n  width: 56px;\r\n  display: flex;\r\n  position: absolute;\r\n  right: 10%;\r\n}\r\n.btn-login img {\r\n  height: 30px;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
