.back-div[data-v-76ac624a] {
  width: 100%;
  height: 100%;
  background: #000000b0;
  position: fixed;
  z-index: 4;
  top: 0;
  right: 0;
}
.window-treners-block[data-v-76ac624a] {
  /* position: absolute; */
  /* right: 15%; */
  /* top: 8%; */
  width: 70%;
  position: relative;
  margin: 10px auto;
  background-color: white;
  border-radius: 20px;
}
.window-treners-ul[data-v-76ac624a] {
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.window-login-title[data-v-76ac624a] {
  font-size: 1.6em;
  font-weight: 600;
}
.treners-filter-block[data-v-76ac624a] {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
  padding: 6px 0;
}
.treners-staff-title[data-v-76ac624a] {
  font-size: 1.5em;
  font-weight: 600;
}
.treners-staff-img[data-v-76ac624a],
.treners-filter-img[data-v-76ac624a] {
  width: 36px;
  padding: 5px;
  border-radius: 10px;
  cursor: pointer;
}
.treners-filter-img[data-v-76ac624a]:hover {
  background-color: #ccccccfa;
}
.filter-active[data-v-76ac624a] {
  background-color: #b9a3d3fa;
}
.filter-active[data-v-76ac624a]:hover {
  background-color: #a372dbfa;
}
.treners-ul-content[data-v-76ac624a] {
  max-height: 700px;
  overflow-y: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}
.treners-ul-content[data-v-76ac624a]::-webkit-scrollbar {
  width: 4px;
  height: 4px;
  border-radius: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.treners-ul-content[data-v-76ac624a]::-webkit-scrollbar-track {
  background: rgb(152, 152, 255);
  border-radius: 20px;
}
.treners-ul-content[data-v-76ac624a]::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background-color: #2d015f50;
}
.window-treners-img[data-v-76ac624a] {
  position: absolute;
  background-size: cover;
  z-index: 4;
  width: 14px;
  height: 14px;
  right: 10px;
  top: 10px;
  transition: all 0.3s;
  cursor: pointer;
}
.window-treners-img[data-v-76ac624a]:hover {
  top: 8px;
}
