<template>
  <div class="window-login">
    <div class="window-login-title">Вход</div>
    <div class="window-login-form">
      <div class="form-control">
        <label for="phoneLogin">Телефон</label>
        <input v-model="phone" type="text" placeholder="Телефон" />
      </div>
      <div class="form-control">
        <label for="passLogin">Пароль</label>
        <input v-model="pass" type="password" placeholder="Пароль" />
      </div>
      <div class="form-control-btn">
        <button @click="logining" class="login-form-btn">Войти</button>
        <button
          @click="
            () => {
              $emit('unmountLoginForm');
            }
          "
          class="cancel-btn"
        >
          Отмена
        </button>
      </div>
    </div>
  </div>
  <Teleport to="body">
    <Message
      v-if="openMessage === true"
      :title="titleForMsg"
      :note="noteForMsg"
      :className="classNameForMsg"
      @close-message="openMessage = false"
    ></Message>
  </Teleport>
</template>
<script setup>
import { inject, ref } from "vue";
import Message from "../../message/message.vue";

const emits = defineEmits(["unmountLoginForm"]);

const checkUser = inject("checkUser");

const phone = ref("");
const pass = ref("");
const titleForMsg = ref("");
const noteForMsg = ref("");
const classNameForMsg = ref("");
const openMessage = ref(false);

function isValidNumber(phone) {
  const pattern = /7\d{10}/g;
  return pattern.test(phone);
}

function isValidPass(pass) {
  const pattern = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[a-zA-Z0-9]{8,20}$/;
  return pattern.test(pass);
}

async function logining() {
  let noteErr = "";
  if (!isValidNumber(phone.value)) {
    titleForMsg.value = "Ошибка заполнения!";
    noteErr += "Неверный номер телефона!";
    classNameForMsg.value = "error";
    openMessage.value = true;
  }
  if (!isValidPass(pass.value)) {
    titleForMsg.value = "Ошибка заполнения!";
    noteErr +=
      'Неверный пароль: <br>Пароль должен состоять из: <br>Минимум одной заглавной буквы <br> Минимум одной строчной буквы <br>Минимум из одной цифры"';
    classNameForMsg.value = "error";
    openMessage.value = true;
  }

  noteForMsg.value = noteErr;
  if (noteErr.length === 0) {
    openMessage.value = false;
    const res = await fetch(`/api/login`, {
      headers: {
        Authorization: `Basic ${btoa(`${phone.value}:${pass.value}`)}`,
      },
    });
    // const loginEvent = new CustomEvent("login", {
    // detail: phone.value,
    // });
    // document.dispatchEvent(loginEvent);
    checkUser();
    emits("unmountLoginForm");
  }
}
</script>
<style scoped>
.window-login {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.window-login-title {
  font-size: 2em;
  font-weight: 600;
}
.window-login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
}
.form-control {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 100%;
}
.form-control label {
  font-size: 1.1em;
  text-align: left;
}
.form-control input {
  padding: 6px 12px;
  font-size: 1.2em;
  border-radius: 10px;
  outline: none;
  border: 1px solid;
  background-color: #b9a3d3fa;
  transition: all 0.3s;
}
.form-control input:hover,
.form-control input:focus {
  background-color: #a372dbfa;
}
.login-form-btn {
  padding: 8px 14px;
  border-radius: 10px;
  border: none;
  background-color: #b9a3d3fa;
  font-size: 1em;
  transition: all 0.3s;
}
.login-form-btn:hover {
  background-color: #a372dbfa;
  box-shadow: 0 0 3px 1px #2d015fe7;
}
.form-control-btn {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.cancel-btn {
  padding: 8px 14px;
  text-align: center;
  border-radius: 10px;
  font-size: 1em;
  /* margin-top: 10px; */
  transition: 0.3s;
  cursor: pointer;
  background-color: #cbcacce7;
  border: none;
}
.cancel-btn:hover {
  color: #2d015fe7;
  text-decoration: dashed;
  box-shadow: 0 0 3px 1px #2d015fe7;
}
</style>
