y = ref(\"\");\r\nconst refactorDay = ref(\"\");\r\n\r\nconst date = new Date();\r\nconst long = {\r\n  weekday: \"long\",\r\n  day: \"numeric\",\r\n  month: \"numeric\",\r\n  year: \"numeric\",\r\n};\r\nconst short = {\r\n  weekday: \"short\",\r\n  day: \"numeric\",\r\n  month: \"numeric\",\r\n  year: \"numeric\",\r\n};\r\nwatch(winWidth, () => {\r\n  winWidth.value = window.innerWidth;\r\n  if (winWidth.value > 900) {\r\n    createWeekCalendar(date, long);\r\n  } else {\r\n    createWeekCalendar(date, short);\r\n  }\r\n});\r\n\r\nconst onResize = () => (winWidth.value = window.innerWidth);\r\n\r\nfunction refactorStringDay(day) {\r\n  const usuallyDay = day.split(\" \")[1];\r\n  activeDay.value = usuallyDay.split(\".\").reverse().join(\"-\");\r\n  refactorDay.value = activeDay.value;\r\n}\r\n\r\nonMounted(async () => {\r\n  createWeekCalendar(date, long);\r\n  window.addEventListener(\"resize\", onResize);\r\n  const res = await fetch(\"/api/programs/getschedule\");\r\n  recordData.value = await res.json();\r\n  refactorStringDay(week.value[0]);\r\n});\r\n\r\nfunction createWeekCalendar(startDate, format) {\r\n  const start = new Date(startDate);\r\n  const weekDays = [];\r\n  // Заполняем массив днями недели\r\n  for (let i = 0; i < 7; i++) {\r\n    const day = new Date(start);\r\n    day.setDate(start.getDate() + i);\r\n    weekDays.push(day.toLocaleDateString(\"ru-RU\", format));\r\n  }\r\n  week.value = weekDays;\r\n}\r\n</script>\r\n<style scoped>\r\n.datetime-programs-content {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  justify-content: center;\r\n  gap: 20px;\r\n  margin-top: 20px;\r\n  width: 100%;\r\n}\r\n\r\n.datetime {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-items: center;\r\n  width: 100%;\r\n}\r\n.datetime-block {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  width: 1400px;\r\n}\r\n.datetime-title {\r\n  font-size: 2em;\r\n  font-weight: 600;\r\n  padding: 12px;\r\n}\r\n.datetime-content {\r\n  width: 1400px;\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: space-evenly;\r\n}\r\n.datetime-day,\r\n.datetime-number {\r\n  font-size: 1.5em;\r\n  text-transform: capitalize;\r\n}\r\n.datetime-content-block {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 5px;\r\n  align-items: center;\r\n  border-radius: 20px;\r\n  padding: 10px 20px;\r\n  box-shadow: 0px 0px 20px #b9a3d3fa;\r\n  transition: 0.3s;\r\n}\r\n.datetime-content-block-active {\r\n  background-color: #a372dbfa;\r\n}\r\n.datetime-content-block:hover {\r\n  background-color: #a372dbfa;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
