ax = parseInt(rangeMax.value);\r\n\r\n  // Обеспечиваем, чтобы минимальное значение не превышало максимальное\r\n  if (min > max) {\r\n    rangeMin.value = max;\r\n  }\r\n\r\n  // Обновляем положение ползунка\r\n  const minPercent = (rangeMin.value / rangeMin.max) * 100;\r\n  const maxPercent = (rangeMax.value / rangeMax.max) * 100;\r\n\r\n  slider.value.style.left = `${minPercent}%`;\r\n  slider.value.style.width = `${maxPercent - minPercent}%`;\r\n\r\n  // Обновляем отображаемые значения\r\n  minValue.textContent = rangeMin.value;\r\n  maxValue.textContent = rangeMax.value;\r\n}\r\n</script>\r\n<style scoped>\r\n/* Фильтр */\r\n.filter {\r\n  width: 100%;\r\n  /* display: none; */\r\n  transition: all 3s;\r\n  margin: 30px 0;\r\n  /* opacity: 0; */\r\n}\r\n.filter-block {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  /* width: 1400px; */\r\n  gap: 30px;\r\n  margin: 0 auto;\r\n  border-radius: 20px;\r\n  background-color: #cccccc62;\r\n  padding: 10px;\r\n}\r\n.filter-block-title {\r\n  font-size: 2em;\r\n  font-weight: 600;\r\n}\r\n.filter-category-title {\r\n  font-size: 1.6em;\r\n  font-weight: 600;\r\n}\r\n.filter-category-row {\r\n  display: flex;\r\n  flex-direction: row;\r\n  gap: 20px;\r\n}\r\n.filter-category-block {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 20px;\r\n  width: 100%;\r\n}\r\n.filter-category-item,\r\n.filter-gender-item {\r\n  font-size: 1.2em;\r\n  padding: 5px 10px;\r\n  border-radius: 20px;\r\n  border: 1px solid #2d015fe7;\r\n  transition: all 0.3s;\r\n}\r\n.filter-category-item:hover,\r\n.filter-gender-item:hover {\r\n  background-color: #b9a3d3fa;\r\n}\r\n.gender-item-active,\r\n.category-item-active {\r\n  background-color: #a372dbfa;\r\n  color: white;\r\n}\r\n\r\n/* Ползунок */\r\n.input-range-row {\r\n  padding: 10px;\r\n}\r\n.value {\r\n  font-size: 1.5em;\r\n  width: 30px;\r\n}\r\n/* Range Slider */\r\n.input-ranges[type=\"range\"] {\r\n  width: 260px;\r\n  height: 30px;\r\n  ov