const checkUser = inject("checkUser");

async function logout() {
  if (username.value) {
    await fetch(`/api/logout`);
    username.value = undefined;
    // const logoutEvent = new CustomEvent("logout");
    // document.dispatchEvent(logoutEvent);
    checkUser();
  }
}
